/* public/StyleSheet/Services.module.css */

/* ---- Page Background (Optional but helps cards stand out) ---- */
/* If you have a global stylesheet, consider setting a light gray background for the body
   body { background-color: #f0f2f5; }
*/

.service {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); /* Min width for cards */
  gap: 2rem; /* Increased space between cards */
  padding: 2.5rem; /* Padding around the grid of cards */
  max-width: 1200px;
  margin: 3rem auto; /* More margin for the section */
  min-height: calc(100vh - 220px); /* Adjust based on your Navbar/Footer combined height */
}

/* ---- THE CARD ---- */
.service > div {
  background-color: #ffffff; /* White background for the card */
  border: 1px solid #e9ecef; /* Lighter border for a softer look */
  border-radius: 12px; /* More rounded corners */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.04), 0 5px 15px rgba(0, 0, 0, 0.08); /* Softer, layered shadow */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out, border-color 0.3s ease;
  display: flex; /* To center link content */
  flex-direction: column; /* Allows for future content stacking (e.g., icon above text) */
  align-items: center;
  justify-content: center;
  overflow: hidden; /* Important for border-radius and potential inner elements */
  cursor: pointer; /* Change cursor to indicate the whole card is clickable */
  position: relative; /* For potential pseudo-elements like accents */
}

/* ---- CARD HOVER EFFECT ---- */
.service > div:hover {
  transform: translateY(-8px) scale(1.02); /* Lift and slightly scale the card */
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.06), 0 10px 25px rgba(0, 0, 0, 0.1); /* Enhanced shadow on hover */
  border-color: #007bff; /* Optional: highlight border on hover (use your primary color) */
}

