/* ---- LINK STYLING (Content inside the Card) ---- */
.service > div > a {
  display: flex; /* Use flex for better control if you add icons later */
  align-items: center;
  justify-content: center;
  padding: 2.5rem 1.5rem; /* Generous padding for the clickable area */
  text-decoration: none;
  color: #343a40; /* Darker text color for better readability */
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
  width: 100%;
  height: 100%;
  transition: color 0.2s ease;
  z-index: 1; /* Ensure link is above pseudo-elements if used */
}

/* Change link text color when the CARD (div) is hovered */
.service > div:hover > a {
  color: #007bff; /* Use your primary color */
}

/* ---- RESPONSIVE ADJUSTMENTS ---- */
@media (max-width: 992px) { /* Tablets */
  .service {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
    padding: 2rem;
  }
  .service > div > a {
    font-size: 1.15rem;
    padding: 2rem 1.2rem;
  }
}

@media (max-width: 768px) { /* Smaller tablets / Large phones */
  .service {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  }
   .service > div > a {
    font-size: 1.1rem;
    padding: 1.8rem 1rem;
  }
}

@media (max-width: 576px) { /* Most Phones */
  .service {
    grid-template-columns: 1fr; /* Stack cards vertically */
    padding: 1.5rem 1rem; /* Reduce padding on smaller screens */
    gap: 1.5rem;
  }
   .service > div > a {
    font-size: 1.05rem;
    padding: 1.5rem;
  }
  .service > div:hover {
    transform: translateY(-5px) scale(1.01); /* Slightly less dramatic hover on mobile */
  }
}